:root {
  --bg-color: #FFFFFF;
  --text-color: #000000;
  transition: background-color 1s ease;
}

html{
  display:flex;
  height:100vh;
  justify-content: center;
  align-items: center;
  background-color:var(--bg-color);
}

#quote-box {
  width: 600px;
  height:auto;
  background:white;
  border: none;
  padding:40px 20px;
}

#quote-box h1 {
  text-align: center;
  color:var(--text-color);
}

.text-content {
  display: flex;
  flex-direction: column;
}

.text-content p {
  margin-top:20px;
  text-align: end;
  margin-right:20px;
  color:var(--text-color);
}

.footer {
  margin-top:50px;
  padding:30px 20px;
  display: flex;
  justify-content: space-between;
}

.icon-container{
  display: flex;
  gap:5px;
}

.ico-box{
  display: flex;
  align-items: center;
  justify-content: center;
  width:40px;
  /* padding:10px; */
  background-color: var(--bg-color)
}

button {
  width: 100px;
  height:40px;
  padding:5px 10px;
  color:white;
  background: var(--bg-color);
  border:none;
}

.twitter-icon {
  height:20px;
  cursor: pointer;
  background-color: var(--bg-color);
  color:white;
}